.expense-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    /* box-shadow: 0 2px 8px rgba(0,0,0,0.25); */
    padding: 0.5rem;
    margin: 1rem 0;
    /* border-radius: 12px; */
    /* background-color: #4b4b4b; */
    background-color: #34495e;
}

.expense-item-desc {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: flex-end;
    flex-flow: column-reverse;
    justify-content: flex-start;
    flex: 1;
}

.expense-item h2 {
    color: #3a3a3a;
    font-size: 1rem;
    flex: 1;
    margin: 0 1rem;
    color: #fff;
}

.expense-item-price {
    font-size: 1rem;
    font-weight: bold;
    color: #fff;
    /* background-color: #40005d; */
    background-color: #30336b;
    /* background-color: #009688; */
    border: 1px solid #fff;
    padding: 0.5rem;
    border-radius: 12px;
}

@media screen and (min-width: 580px){
    .expense-item-desc {
        flex-direction: row;
        align-items: center;
        justify-content: flex-start;
        flex: 1;
    }
    .expense-item-desc h2 {
        font-size: 1.25rem;
    }
    .expense-item-price {
        font-size: 1.25rem;
        padding: 0.5 1.5rem;
    }
}